.wysiwyg {
  @include null-margins;

  &.-split-list ul {
    @include reset-list;
    font-size: 1.3rem;
    margin-top: unit(2);

    @include respond-to('small') {
      column-count: 2;
      column-gap: unit(2);
    }

    @include respond-to('medium') {
      column-gap: unit(3);
    }

    @include respond-to('large') {
      column-gap: unit(5);
    }

    li {
      break-inside: avoid;

      @include respond-to('small') {
        + li {
          margin-top: unit(2);
        }
      }
    }
  }

  img {
    height: auto;
    max-width: 100%;
  }

  table,
  th,
  td {
    border: 1px solid $border--light;
  }

  table {
    border-collapse: collapse;
    margin: unit(2) 0;
    width: 100%;

    tr {
      &:nth-child(odd) {
        td {
          background-color: $color--cararra;
        }
      }

      &:nth-child(even) {
        td {
          background-color: $color--white;
        }
      }
    }

    td {
      @include null-margins;
      border-color: $border--light;
      line-height: 1.4;
      padding: unit(0.6) unit(1.5);
      text-align: left;
      vertical-align: top;

      a {
        @include hover-underline;
        color: $color--venice-blue;
      }

      h2,
      h3,
      h4 {
        display: block;
        padding: unit(0.5) 0;
        text-align: center !important;
        width: 100%;
      }
    }
  }
}

img,
.mce-blockquote,
.mce-stat,
.wp-caption {
  img {
    height: auto;
    width: 100%;
  }

  &.alignleft,
  &.alignright {
    display: block;
    height: auto;
    margin: unit(2) auto;
    max-width: none !important;
    width: 100% !important;

    @include respond-to('small') {
      margin-bottom: 0.5em;
      margin-top: 0.5em;
      max-width: 44% !important;
    }

    @include respond-to('medium-large') {
      max-width: none !important;
      width: 44% !important;
    }
  }

  &.alignleft {
    @include respond-to('small') {
      float: left;
      margin-right: unit(2);
    }

    @include respond-to('medium-large') {
      margin-left: -21%;
    }
  }

  &.alignright {
    @include respond-to('small') {
      float: right;
      margin-left: unit(2);
    }

    @include respond-to('medium-large') {
      margin-right: -21%;
    }
  }
}

.wp-caption-text {
  @include caption-long;
  margin: 0;
  padding-left: 0;
  padding-right: 0;
  text-align: left;
}

.mce-blockquote {
  color: $color--jelly-bean;
  font: 2rem/1.6 $font-family--serif;

  &.alignleft,
  &.alignright {
    @include respond-to('small') {
      margin-top: 0;
    }
  }

  &.aligncenter {
    margin: unit(2) auto;

    @include respond-to('small') {
      text-align: center;
    }

    @include respond-to('medium') {
      max-width: 78rem;
    }
  }
}

.mce-blockquote__attribution {
  color: $color--tuatara;
  display: block;
  font: $font-weight--bold 1.2rem/1.4 $font-family--sans;
  margin-top: unit(0.5) !important;
}

.mce-stat {
  @include null-margins;
  text-align: center;

  &.alignleft,
  &.alignright {
    @include respond-to('small') {
      margin-top: 0;
    }
  }

  &.aligncenter {
    margin: unit(2) auto;
  }
}

.mce-stat__wrapper {
  @include smooth-text;
  align-items: center;
  border-radius: 50%;
  color: $color--white;
  display: flex;
  flex-direction: column;
  height: unit(6);
  justify-content: center;
  margin: 0 auto unit(1);
  width: unit(6);

  &.-brown {
    background-color: $color--fuscous-gray;
  }

  &.-blue {
    background-color: $color--jelly-bean;
  }

  &.-green {
    background-color: $color--olive-drab;
  }

  &.-tan {
    background-color: $color--cararra;
    color: $color--tuatara;
  }
}

.mce-stat__number {
  font: 5rem/1 $font-family--kapra-condensed;
}

.mce-stat__modifier {
  font: 1.6rem/1 $font-family--kapra-expanded;
  text-transform: uppercase;
}

.mce-stat__caption {
  font-size: 1.3rem;
  line-height: 1.4;
}
