  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.a11y-sr-only-focusable:active,
[data-whatintent='keyboard'] .a11y-sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  width: auto; }

.a11y-skip-link {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #014054;
  color: #fff;
  display: block;
  font-weight: 700;
  left: 0;
  padding: 20px;
  position: absolute;
  top: 0;
  z-index: 1000; }
  .a11y-skip-link:active, .a11y-skip-link:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    width: auto;
    background-color: #014054;
    color: #fff; }

