@font-face {
  font-family: 'Kapra Neue Condensed';
  font-style: normal;
  font-weight: normal;
  src: url('#{$path}/fonts/kapraneue-regularcondensed-webfont.woff2')
      format('woff2'),
    url('#{$path}/fonts/kapraneue-regularcondensed-webfont.woff') format('woff');
}

@font-face {
  font-family: 'Kapra Neue Expanded';
  font-style: normal;
  font-weight: normal;
  src: url('#{$path}/fonts/kapraneue-mediumexpanded-webfont.woff2')
      format('woff2'),
    url('#{$path}/fonts/kapraneue-mediumexpanded-webfont.woff') format('woff');
}

/*
 * headings
 */

.heading-h1,
.heading-h2,
.heading-h3,
.heading-h4 {
  &.-centered {
    text-align: center;
  }
}

.heading-h1 {
  font: 4.5rem/1 $font-family--kapra-condensed;
  text-transform: uppercase;

  @include respond-to('medium') {
    font-size: 5.4rem;
  }

  @include respond-to('medium-large') {
    font-size: 6.3rem;
  }

  @include respond-to('large') {
    font-size: 7.2rem;
  }
}

.wysiwyg h2,
.mce-content-body h2,
.heading-h2 {
  font: 3.6rem/1.17 $font-family--kapra-condensed;
  text-transform: uppercase;

  @include respond-to('medium-large') {
    font-size: 4.8rem;
  }
}

.mce-content-body h2 {
  font-size: 4.8rem;
}

.wysiwyg h3,
.mce-content-body h3,
.heading-h3 {
  color: $color--venice-blue;
  font: $font-weight--normal 2.4rem/1.17 $font-family--sans;

  .-light-gray &,
  &.-gray {
    color: $color--fuscous-gray;
  }

  @include respond-to('medium-large') {
    font-size: 3rem;
  }
}

.mce-content-body h3 {
  font-size: 3rem;
}

.wysiwyg h4,
.mce-content-body h4,
.heading-h4 {
  font: 1.6rem/1.5 $font-family--kapra-expanded;
  text-transform: uppercase;

  &.-blue {
    color: $color--jelly-bean;
  }

  &.-dark-blue {
    color: $color--sherpa-blue;
  }

  + p {
    margin-top: unit(-0.5);
  }

  @include respond-to('medium-large') {
    &:not(.-smaller) {
      font-size: 1.8rem;
    }
  }
}

/*
 * misc. type treatments
 */

.lede {
  font: 1.6rem/1.75 $font-family--serif;

  @include respond-to('medium-large') {
    font: 1.8rem/1.75 $font-family--serif;

    &.-large {
      font-size: 2.4rem;
      line-height: 1.4;
    }
  }

  strong {
    font-weight: 700;
  }
}

.mce-content-body .lede {
  font-size: 1.8rem;
}

div.lede {
  @include null-margins;
  margin: unit(1) 0;
}

.centered-heading {
  @include null-margins;
  border-bottom: 1px solid $border--light;
  margin: 0;
  padding-bottom: unit(2);
  text-align: center;

  @include respond-to('medium-large') {
    margin: 0 unit(2);
  }
}
